.wrapper-1 {
    margin-right: auto;
    margin-left: auto;
    max-width: 960px;
    padding: 5%;
}

.wrapper-2 {
    margin: 0 15%;
}

.bg-white {
    background-color: white;
    border-radius: 15px;
    padding: 5% 2%;
    height: 450px;
}

.hjem-bg {
    background-color: #2c2c2c;
    border-radius: 15px;
}


/* ON / OFF */

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #2c2c2c;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked+.slider {
    background-color: #ffcd29;
}

input:focus+.slider {
    box-shadow: 0 0 1px #ffcd29;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}


/* Rounded sliders */

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}